button {
  margin-top: 20%;
  margin-left: 20%;
  height: 60px;
  width: 250px;
  border-radius: 30px;
  background-color: black;
  color: white;
  font-size: xx-large;
  text-transform: uppercase;
  filter: drop-shadow(5px 5px 10px #ffffff);
}
/* Style the links inside the navigation bar */
.topnav {
  margin: 0;
  padding: 0;
  background-color: black;
  height: 50px;
  color: white;
}
.topnav a {
  float: left;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
  /* background-color: #ddd; */
  color: white;
}

/* Add a color to the active/current link */
.topnav a {
  color: white;
}
  